/**
 * InheritedReference.
 */
import BuildingBlockReference from './BuildingBlockReference';

export default class InheritedReference extends BuildingBlockReference {
    constructor(entity, tag, inherited = false, baseEntity, baseReference, rootReference) {
        super(entity, tag);
        this.entity = entity;
        this.tag = tag;
        this.inherited = inherited;
        this.baseEntity = baseEntity;
        this.baseReference = baseReference;
        this.rootReference = rootReference;
    }
}


