import React from 'react';
import { Link } from 'react-router-dom';

import AppService from '../app/AppService';
import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import IdInput from '../common/IdInput';
import './View.css';

class CanBeA extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false
    };
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onCreate(entityName) {
    this.props.entity.execute("new", entityName);
    this.setState((prevState) => {
        return { showSelector: false };
    });
    this.props.refresh();
  }

  onDelete(param) {
    this.props.entity.execute("removeSubType", param.id);
    this.props.refresh();
  }

  onAdd(ref) {
    this.setState((prevState) => {
        return { showSelector: true };
    });
  }

  renderSuperTypes(subTypes) {
    if (subTypes && subTypes.size > 0) {
      let list = [];
      for (let bId of subTypes.keys()) {
        const subType = subTypes.get(bId);
        const name = this.formatName(subType);
        const subSubTypes = subType.execute("listSubTypes");
        list.push(<li key={subType.id} className="browse-behavior-row">
          <Link to={subType.id}>{name}</Link>
          <span key="deleteOveride" className="row-right"><button 
            className="btn-add" onClick={this.onDelete.bind(this, subType)}><i 
            className="fa fa-minus red"></i></button></span>
          {subSubTypes?<span className="row-right count">{subSubTypes.size}</span>:null}
          </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component);
    const subTypes = this.props.entity.execute("listSubTypes");
    const list = this.renderSuperTypes(subTypes);
    const uid = this.bb.generateUID();
    return (
      <div>
        <BrowseBehaviorBox title={name} count={subTypes && subTypes.size}
            onAdd={this.onAdd.bind(this)}>
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No SubTypes</div>}
        </BrowseBehaviorBox>
        <IdInput key={uid}
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onCreate={this.onCreate.bind(this)}/>
      </div>
    );
  }
}

export default CanBeA;


