import React from 'react';
import { Link } from 'react-router-dom';

import AppService from '../app/AppService';
import ViewComponent from './ViewComponent';
import Graph from '../core/Graph';

class EntityPath extends ViewComponent {

    browse( ancestor, className ) {
        if ( this.props.onClick && className === "active" ) {
            this.props.onClick( ancestor);
        }
    }
    
    renderSingle() {
        const entity = this.props.data;
        const base = this.props.base;
        let enableLinks = (base === null);

        let path = [];
        let ancestors = entity.execute( "listAncestors" ).toArray();
        while ( ancestors.length ) {
            let ancestor = ancestors.pop();
            if ( ancestor === base ) {
                enableLinks = true;
            }
            const ancestorName = ancestor.execute( "getName" ) || <i>&lt; { ancestor.id } &gt;</i>;
            let className = "el";
            if (ancestors.length) {
                if ( this.props.type === "parents" && enableLinks ) {
                    className = "active";
                } else {
                    // no click on parents
                    className = "el";
                }
            } else {
                if ( this.props.type === "parents" ) {
                    // no click on current
                    className = "el";
                } else {
                    className = "active";
                }
            }
            //path.push( <span className={className} key={ancestor.id} onClick={this.browse.bind( this, ancestor, className ) }><span className="slash">/</span>{ ancestorName }</span> );
            const pathElement = (className === "el")? 
                <Link key={ancestor.id} to={ancestor.id}>{ancestorName}</Link> : 
                <span key={ancestor.id}>{ancestorName}</span> ;
            path.push( <span className={className} key={ancestor.id}><span className="slash">/</span>{pathElement}</span> );
        }
        return <span className="path">{ path }</span>
    }

    render() {
        const entity = this.props.data;
        const self = this;
        if (entity != null) {
            let ancestors = entity.execute( "getInheritanceGraph");
            if (!ancestors.size) {
                return;
            }
            //TODO deal with aliases of root!
            let rootObj = entity.entity || entity;
            if (ancestors.has(this.bb.root.id)) {
                rootObj = ancestors.get(this.bb.root.id);
            }
            let table = [];
            let row = [];
            let idx = 0;
            let pos = true;
            const graph = new Graph(rootObj);
            let maxRowspan = 0;
            graph.traversePreorder(function(node) {
                if (!node.children) {
                    maxRowspan++;
                }
            });
            if (maxRowspan === 1) {
                return <span className="path">{this.renderSingle()}</span>;
            }
            graph.traversePreorder(function(node) {
                let id = node.obj.id;
                if (node.children) {
                    let colspan = node.spos - node.pos;
                    if (!colspan || colspan < 1) {
                        colspan = 1;
                    }
                    let rowspan = (node === rootObj)? maxRowspan : 1;
                    if (!pos) {
                        const firstSpan = rootObj.pos - node.spos;
                        if (firstSpan) {
                            row.push(<td key={id+'row'} colSpan={firstSpan}></td>);
                        }
                    }
                    const name = self.formatName(node.obj);
                    row.push(<td key={id} colSpan={colspan} rowSpan={rowspan} valign='top' className="nowrap">
                        <span className="slash">/</span><Link to={id}>{name}</Link></td>);
                    //row.push(<td key={id} colSpan={colspan} rowSpan={rowspan}>/{id} ({node.spos}-{node.pos})</td>);
                    pos = true;
                } else {
                    row.push(<td key={id+'.'}  valign='top' className="active nowrap">
                        <span className="slash">/</span>{self.formatName(entity)}</td>);
                    table.push(<tr key={idx++}>{row}</tr>);
                    row = [];
                    pos = false;
                }
            });
            return <span className="path">
                <table border={0}><tbody>{ table }</tbody></table>
                </span>;
        } else {
            return null;
        }
    }
}

export default EntityPath



