import React from 'react';
import { Link } from 'react-router-dom';

import AppService from '../app/AppService';
import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import TypeSelector from '../common/TypeSelector';
import BuildingBlockMap from '../core/BuildingBlockMap';

class Has extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false,
        selectorType: null
    };
    this.setTag(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.setTag(nextProps);
  }

  setTag(props) {
    const tags = props.entity.execute("listAllReferences", props.component);
    const tag = tags.size? tags.values().next().value : null;
    const hasTag = tag.rootReference || tag.entity;

    this.hasTag = hasTag;
    this.tag = tag;
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onRefSelected(data, base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    const tagRef = this.state.selectorType;
    const tag = /*tagRef.baseReference || */tagRef.entity;
    this.props.entity.execute("addReference", entity, tag);
    this.props.refresh();
    return true;
  }

  onRefTypeSelected(data, base) {
    console.log('Selected HAS TYPE '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    this.props.entity.execute("addReference", entity, this.hasTag);
    this.props.refresh();
    return true;
  }

  onRefUpdateSelected(curRef, data, base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }

    const hasEntityUpdate = entity.execute("newReference", 
            curRef.tag, false, curRef.baseEntity, curRef.entity);
    this.props.entity.execute("updateReference", hasEntityUpdate);
    this.props.refresh();
    return true;
  }

  onRefTypeUpdateSelected(data,base) {
    console.log('Selected HAS '+ data);
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }

    const hasEntityUpdate = entity.execute("newReference", 
            base.tag, false, base.baseEntity, base.entity);
    this.props.entity.execute("updateReference", hasEntityUpdate);
    this.props.refresh();
    return true;
  }

  onDelete(ref) {
    console.log('Delete HAS '+ ref);
    //TODO, check if any member with this tag first..
    this.props.entity.execute("removeReference", ref.id);
    this.props.refresh();
  }

  onUpdate(crtRef) {
    console.log('Update HAS '+ crtRef);
    const userEntity = this.bb.systemRoot.execute( "getEntity", 'userEntity' );
    let baseRef = crtRef.inherited? crtRef.entity : (crtRef.baseReference || userEntity);
    this.showReferenceSelector(baseRef, this.onRefUpdateSelected.bind(this, crtRef));
  }

  onUpdateType(ref) {
    this.showReferenceSelector(ref, this.onRefTypeUpdateSelected);
  }

  onAdd(ref) {
    this.showReferenceSelector(ref, this.onRefSelected);
  }

  onAddType() {
    const userEntity = this.tag.entity;
    this.showReferenceSelector(userEntity, this.onRefTypeSelected);
  }

  showReferenceSelector( selectorType, aSelectorFn ) {
    const selectorFn = aSelectorFn.bind(this);
    this.setState((prevState) => {
        return { showSelector: true, selectorType, selectorFn };
    });
  }

  renderOriginContext(reference) {
    if (reference.baseEntity) {
      const baseEntity = this.formatName(reference.baseEntity);
      if (reference.inherited) {
        if (reference.baseReference) {
          const baseReference = this.formatName(reference.baseReference);
          return <span key="inherited">
            <span className='inheritedOverride'>inherits override
                </span> from {baseEntity}, base reference: {baseReference}</span>; 
        } else {
          return <span key="inherited"><span className='inherited'>inherits
                  </span> from {baseEntity}</span>; 
        }       
      }
      const baseReference = this.formatName(reference.baseReference);
      return <span key="overrides"><span className='override'>overrides
        </span> {baseEntity}.<i>{baseReference}</i></span>;
    } else {
      return <span key="overrides"><span className='new-reference'>new reference</span></span>
    }
  }

  renderIcon(reference, error){
    if (reference.baseEntity) {
      if (reference.inherited) {
        if (reference.baseReference) {
          return 'caret-circle-down ' + (error? 'red' : 'blue'); //inherited override
        } else {
          return 'circle ' + (error? 'red' : 'blue'); //inherited
        }       
      }
      return 'dot-circle ' + (error? 'red' : 'orange'); //override
    } else {
      return 'certificate ' + (error? 'red' : 'green'); // new ref
    }
  }

  renderActions(reference){
    /**
     * {isInline?
                  <span className="row-right"><button className="btn-add" onClick={this.onAdd}><i className="fa fa-plus blue"></i></button></span>
                  : null}
     */
    const deleteAction = <span key="deleteOveride" className="row-right"><button 
      className="btn-add" onClick={this.onDelete.bind(this, reference)}><i 
      className="fa fa-minus red"></i></button></span>;
    
    const updateAction = <span key="updateOveride" className="row-right"><button 
      className="btn-add" onClick={this.onUpdate.bind(this, reference)}><i 
      className="fa fa-edit orange"></i></button></span>;
    
    let result = [];
    if (reference.baseEntity) {
      result.push(updateAction);     //override
      if (!reference.inherited) {
          result.push(deleteAction); // inherited & inherited override 
      }
    } else {
      result.push(deleteAction);     // new ref
    }
    return result;
  }

  renderSuperTypes(reference) {
    const superTypes = reference.execute("listSuperTypes");
    if (superTypes && superTypes.size > 0) {
      let list = [];
      for (let bId of superTypes.keys()) {
        const superType = superTypes.get(bId);
        const name = this.formatName(superType);
        list.push(<span key={superType.id}>{name}</span>);
      }
      return <span>{list
          .map(t => <span key={t.key}>{t}</span>)
          .reduce((prev, curr) => [prev, ', ', curr])}
        </span>
    }
  }

  renderReferenceDetails(reference) {
    if (this.props.details) {
      const originContext = this.renderOriginContext(reference);
      //const baseType = this.formatName(reference.execute("getBaseType"));
      return <ul>
          {originContext?<li key={reference.id + '-ctx'}>{originContext}</li>:''}
          <li key={reference.id + '-ext'}>extends {this.renderSuperTypes(reference)}</li>
          {/*<li>Base Type: {baseType}</li>*/}
        </ul>;
    }
  }

  renderReferences(entity, baseTag) {
    const references = entity.execute("listAllReferences", baseTag);
    let count = 0;
    let list = [];
    let processedRefs = {};
    if (references && references.size > 0) {
      for (let bId of references.keys()) {
        const reference = references.get(bId);
        processedRefs[bId] = reference;
        const name = this.formatName(reference);
        //const isInline = true;
        list.push(<li key={reference.id} className="browse-behavior-row">
          <i className={"fa fa-"+this.renderIcon(reference)}></i> <Link 
            to={reference.id}>{name}</Link>
          {this.renderActions(reference)}
          {this.renderReferenceDetails(reference)}
        </li>);
      }
    }
    count = list.length;
    return {list, count, processedRefs};
  }

  renderOtherRefs(entity, references, processedRefs, list) {
    let otherRefs = [];
    const allReferences = entity.execute("listAllReferences");
    const allBehaviors = entity.execute("listAllBehaviors");
    if (allReferences && allReferences.size > 0) {
      for (let rId of allReferences.keys()) {
        const uRef = allReferences.get(rId);
        const isAType = references.has(uRef.id);
        const isAnotherBehavior = allBehaviors.has(uRef.id);
        const isFromAnotherBehavior = uRef.tag && allBehaviors.has(uRef.tag.id);
        if (!processedRefs[rId] && !isAType && !isAnotherBehavior && !isFromAnotherBehavior) {
          const name = this.formatName(uRef);
          const tagName = uRef.tag? <span className="count">{this.formatName(uRef.tag)}</span> : null;
          otherRefs.push(<li key={uRef.id} className="browse-behavior-row">
            <i className={"fa fa-"+this.renderIcon(uRef, true)}></i> <Link 
              to={uRef.id}>{name}</Link> {tagName}
            {this.renderReferenceDetails(uRef)}
          </li>);
        }
      }
    }
    if (otherRefs.length) {
      list.push(
        <BrowseBehaviorBox key='otherRefs' type="sub" title='Other References' count={otherRefs.length}>
          <ul className="browse-behavior-rows">
            {otherRefs}
          </ul>
        </BrowseBehaviorBox>
        );
    }
  }

  renderTypeLabel(newTag, crtTag, rootTag) {
    const crtTagName = this.formatName(crtTag);
    const newTagName = this.formatName(newTag);
    const rootTagName = this.formatName(rootTag);
    let details;
    if (!crtTag || crtTag.id !== newTag.id) {
      if (rootTag && (!crtTag || crtTag.id !== rootTag.id)) {
        details = <span>(now {newTagName}, originally {rootTagName})</span>;
      } else {
        details = <span>(now {newTagName})</span>;
      }
    }
    return <span>{crtTagName} types {details}</span>;
  }

  renderReferenceTypes(entity, hasTag) {
    let processedRefs = {};
    const references = entity.execute("listAllReferences", hasTag, { includeOverriden: true });
    let count = 0;
    let list = [];
    if (references && references.size > 0) {
      let tagMap = new BuildingBlockMap();
      for (let bId of references.keys()) {
        const reference = references.get(bId);
        const rootTag = reference.rootReference || reference.entity;
        tagMap.set(rootTag.id, reference.entity);
      }
      for (let bId of references.keys()) {
        const reference = references.get(bId);
        const rootTag = reference.rootReference || reference.entity;
        const newestTag = tagMap.get(rootTag.id);
        const isNewest = (reference.id === newestTag.id);
        const name = this.renderTypeLabel(newestTag, reference, rootTag);
        let ref = this.renderReferences(entity, reference.entity);
        processedRefs = Object.assign(processedRefs, ref.processedRefs);
        if (isNewest || ref.count) {
        const addFn = (isNewest)? this.onAdd.bind(this, reference) : null;
        const updateFn = (isNewest)? this.onUpdate.bind(this, reference) : null;
        const deleteFn = !(reference.baseEntity && reference.inherited)? this.onDelete.bind(this, reference) : null;
        count ++;
        list.push(
          <BrowseBehaviorBox key={reference.id} type="sub" title={name} count={ref.count} 
            onAdd={addFn}
            onUpdate={updateFn}
            onDelete={deleteFn}>
              {ref.list?
                <ul className="browse-behavior-rows">
                  {ref.list}
                </ul>
                : <div className="browse-behavior-row">No References</div>}
            </BrowseBehaviorBox>
          );
        }
      }
    }
    this.renderOtherRefs(entity, references, processedRefs, list);

    if (list.length) {
      return {list, count};
    }
  }

  render() {
    const name = <span>{this.formatName(this.props.component)} ({this.formatName(this.tag.entity)})</span>;
    const {list, count} = this.renderReferenceTypes(this.props.entity, this.hasTag);

    return (
      <div>
        <BrowseBehaviorBox title={name} count={count} 
            onAdd={this.onAddType.bind(this)}>
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No References</div>}
        </BrowseBehaviorBox>
        <TypeSelector
            visible={this.state.showSelector}
            onClose={this.closeSelector.bind(this)}
            onSelect={this.state.selectorFn}
            type={this.state.selectorType} />
      </div>
    );
  }
}

export default Has;


