/**
 * Graph.
 */
export default class Graph  {
    constructor(rootObj) {
        this.root = rootObj;
    }

    traversePreorder(visit) {
        let nodeList = [this.root];
        while (nodeList.length) {
            let node = nodeList.pop();
            visit(node);
            if (node.children) {
                nodeList= nodeList.concat(node.children.toArray());
            }
        }
    }
}


