/**
 * CoreBuildingBlock.
 */
import BootstrappEntity from './BootstrappEntity';

export default class CoreBuildingBlock {
    constructor(id = ".root") {
        this.id = id;
        this.data = new Map();
        const bootstrappEntity = BootstrappEntity.getInstance();
        bootstrappEntity.data.set(id, this);
    }
    // lookup the boostrapped entity from the context
    // the context is intialized when visiting the site and a token gets stores in cookies.
    // Based on the context the right file (guest, member, etc) will be retrieved as a BootstrappEntity.
    execute(action, param, ...extraParams) {
        const bootstrappEntity = BootstrappEntity.getInstance();
        //const params = (extraParams && extraParams.length) ? extraParams : [];
        return bootstrappEntity.execute.bind(this)(action, param, extraParams);
    }
}


