import React from 'react';
import { Link } from 'react-router-dom';

import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import './View.css';

class HasActions extends ViewComponent {

  renderActions(actions) {
    if (actions && actions.size > 0) {
      let list = [];
      for (let bId of actions.keys()) {
        const action = actions.get(bId);
        const name = this.formatName(action);
        list.push(<li key={action.id} className="browse-behavior-row"><Link to={action.id}>{name}</Link></li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component) + this.getTagName();
    const actions = this.props.entity.execute("listActions");
    const list = this.renderActions(actions);
    return (
      <BrowseBehaviorBox title={name} count={actions && actions.size}>
        {list?
          <ul className="browse-behavior-rows">
            {list}
          </ul>
          : <div className="browse-behavior-row">No Actions</div>}
      </BrowseBehaviorBox>
    );
  }
}

export default HasActions;


