import React from 'react';
import { Link } from 'react-router-dom';

import AppService from '../app/AppService';
import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import TypeSelector from '../common/TypeSelector';
import './View.css';

class IsA extends ViewComponent {

  constructor(props) {
    super(props);
    this.state = {
        showSelector: false
    };
  }

  closeSelector() {
    this.setState((prevState) => {
        return {
            showSelector: false
        }
    });
  }

  onAddSuperType(data, base) {
    let entity = data;
    if (!data.id && base.id) {
      entity = base.execute("new", data);
    }
    this.props.entity.execute("addSuperType", entity);
    this.setState((prevState) => {
        return { showSelector: false };
    });
    this.props.refresh();
  }

  onDelete(param) {
    this.props.entity.execute("removeSuperType", param.id);
    this.props.refresh();
  }

  onAdd(ref) {
    const selectorType = this.bb.systemRoot.execute( "getEntity", 'userEntity' );
    const selectorFn = this.onAddSuperType.bind(this);
    this.setState((prevState) => {
        return { showSelector: true, selectorType, selectorFn };
    });
  }

  renderSuperTypes(superTypes) {
    if (superTypes && superTypes.size > 0) {
      let list = [];
      for (let bId of superTypes.keys()) {
        const superType = superTypes.get(bId);
        const name = this.formatName(superType);
        list.push(<li key={superType.id} className="browse-behavior-row"><Link 
          to={superType.id}>{name}</Link>
          <span key="deleteOveride" className="row-right"><button 
            className="btn-add" onClick={this.onDelete.bind(this, superType)}><i 
            className="fa fa-minus red"></i></button></span>
          </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component);
    const superTypes = this.props.entity.execute("listSuperTypes");
    const list = this.renderSuperTypes(superTypes);
    return (
      <div>
        <BrowseBehaviorBox title={name} count={superTypes && superTypes.size}
            onAdd={this.onAdd.bind(this)}>
          {list?
            <ul className="browse-behavior-rows">
              {list}
            </ul>
            : <div className="browse-behavior-row">No SuperTypes</div>}
        </BrowseBehaviorBox>
        <TypeSelector
          visible={this.state.showSelector}
          onClose={this.closeSelector.bind(this)}
          onSelect={this.state.selectorFn}
          type={this.state.selectorType} />
      </div>
    );
  }
}

export default IsA;


