import React from 'react';

import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ComponentView from '../common/ComponentView';
import ViewComponent from '../common/ViewComponent';
import './View.css';

class BehaviorGroup extends ViewComponent {

  renderEntity(behaviors) {
    if (behaviors.size > 0) {
      let list = [];
      for (let bId of behaviors.keys()) {
        const behavior = behaviors.get(bId);
        list.push(<li key={behavior.id} className="browse-behavior-row">
            <ComponentView key={behavior.id} component={behavior} entity={this.props.entity}></ComponentView>
          </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.formatName(this.props.component);
    const behaviors = this.props.entity.execute("listAllBehaviors", this.props.component.id);
    const list = this.renderEntity(behaviors);
    return (
      <BrowseBehaviorBox title={name} type="group" count={behaviors.size}>
        {list?
          <ul className="browse-behavior-rows">
            {list}
          </ul>
          : <div className="browse-behavior-row">No Behaviors</div>}
      </BrowseBehaviorBox>
    );
  }
}

export default BehaviorGroup;


