import React from 'react';

import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import './View.css';

class IsSerializable extends ViewComponent {
  render() {
    const name = this.formatName(this.props.component);
    const json = this.props.entity.execute("getJSON");
    const data = JSON.parse(json);
    return (
      <BrowseBehaviorBox title={name}>
        {json?
          <div className="browse-behavior-row"><pre>{JSON.stringify(data, null, 2) }</pre></div>
          : <div className="browse-behavior-row">No Serialization</div>}
      </BrowseBehaviorBox>
    );
  }
}

export default IsSerializable;


