import React, { Component } from 'react';

import '../style/Common.css';
import HasBehaviors from '../view/HasBehaviors';
import HasActions from '../view/HasActions';
import IsA from '../view/IsA';
import Has from '../view/Has';
import CanBeA from '../view/CanBeA';
import HasAlias from '../view/HasAlias';
import IsAlias from '../view/IsAlias';
import IsIdentifiable from '../view/IsIdentifiable';
import IsSerializable from '../view/IsSerializable';
import BehaviorGroup from '../view/BehaviorGroup';

class ComponentView extends Component {
  components = {
    HasBehaviors: HasBehaviors,
    HasActions: HasActions,
    IsA: IsA,
    Has: Has,
    CanBeA: CanBeA,
    HasAlias: HasAlias,
    IsAlias: IsAlias,
    IsIdentifiable: IsIdentifiable,
    IsSerializable: IsSerializable,

    BehaviorCore: BehaviorGroup,
    RelationCore: BehaviorGroup,
    UserBehavior:  BehaviorGroup
  };

  capitalizefirst(string) {
      return string.charAt(0).toUpperCase() + string.slice(1);
  }

  render() {
    const componentID = this.capitalizefirst(this.props.component.id);
    const ComponentTag = this.components[componentID];
    if (ComponentTag) {
      return (<ComponentTag {...this.props}></ComponentTag>);
    } else {
      return (<div>{componentID} Not found.</div>);
    }
  }
}

export default ComponentView;


