import React from 'react';
import { Link } from 'react-router-dom';

import BrowseBehaviorBox from '../common/BrowseBehaviorBox';
import ViewComponent from '../common/ViewComponent';
import './View.css';

class HasBehaviors extends ViewComponent {
  renderEntity(behaviors) {
    if (behaviors.size > 0) {
      let list = [];
      for (let bId of behaviors.keys()) {
        const behavior = behaviors.get(bId);
        const name = this.formatName(behavior);
        list.push(<li key={behavior.id} className="browse-behavior-row">
          <Link to={behavior.id}>{name}</Link>
        </li>);
      }
      return list;
    }
  }

  render() {
    const name = this.props.component.execute("getName") + this.getTagName();
    const behaviors = this.props.entity.execute("listBehaviors");
    const list = this.renderEntity(behaviors);
    return (
      <BrowseBehaviorBox title={name} count={behaviors.size}>
        {list?
          <ul className="browse-behavior-rows">
            {list}
          </ul>
          : <div className="browse-behavior-row">No Behaviors</div>}
      </BrowseBehaviorBox>
    );
  }
}

export default HasBehaviors;


